        }
    }

    .header-item {
        color: var(--#{$prefix}header-dark-item-color);

        &:hover {
            color: var(--#{$prefix}header-dark-item-color);
        }
    }

    .header-profile-user {
        background-color: rgba($white, 0.25);
    }

    .noti-icon {
        i {
            color: var(--#{$prefix}header-dark-item-color);
        }
    }

    .logo-dark {
        display: none;
    }

    .logo-light {
        display: block;
    }

    .app-search {

        .form-control {
            background-color: var(--#{$prefix}topbar-search-bg);
            color: $white;
        }

        span,
        input.form-control::-webkit-input-placeholder {
            color: rgba($white, 0.5);
        }
    }
}


// ------------------

[data-bs-theme="dark"] {
    #page-topbar {
        background-color: $primary;
        .navbar-brand-box{
            background-color:$primary;
        }
    }
}

[data-bs-theme="dark"] body[data-layout="horizontal"] {
    #page-topbar {
        background-color: $primary;
    }
}